  box-sizing: border-box;
  max-height: 210px;
  border-radius: 5px;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.nice-select .list:hover .option:not(:hover) {
  background-color: rgba(0, 0, 0, 0) !important;
}

